.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}

.App-link {
  color: #61dafb;
}

.App {
  margin: auto;
  width: 50%;
  /* background-color: #90c4ee; */
  display: grid;
  justify-items: center;
  grid-template-areas:
    ". . ."
    ". addTodoForm ."
    ". todoItemList .";
}
